@font-face {
  font-family: arcade;
  src: url(../fonts/ARCADE.TTF);
}

@font-face {
  font-family: matrix;
  src: url(../fonts/Matrix-MZ4P.ttf);
}

@font-face {
  font-family: rock;
  src: url(../fonts/Rockstar-ExtraBold.otf);
}

html{scroll-behavior:smooth}

* {

}


body {
  height: 600vh;
  background-color: rgb(22, 22, 22);
  font-family: 'Montserrat', sans-serif;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}

.logo-url {
  text-decoration: none;
}

.logo {
  font-size: 1.5rem !important;
}

.greeting--text {
  font-family: "rock";
  font-size: 5rem;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

@media screen and (min-width: 700px) {
  
  .header--main {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    background-color: rgb(34, 32, 32);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 90px;
  }
  
  .header--main ul {
    color: white;
  }
  
  .logo {
    font-size: 1.5rem;
    margin-left: 30px;
    cursor: pointer;
    color: white;
    background-image: linear-gradient(90deg, rgb(104, 224, 98), rgb(48, 164, 50));
    background-clip: text;
    color: transparent;
    font-weight: 700;
    text-shadow: none;
  }
  
  .header--main > nav {
    padding: 30px;
    display: flex;
    align-items: center;
    text-align: center;
    list-style: none;
    font-size: 1.5rem;
    color: white;
    text-shadow: none;
  }
  
  .header--main > nav > a {
    margin : 20px;
    text-decoration: none;
    color: white;
  }
  
  main {
    width: 90%;
    margin: 0;
    margin-top: 50px;
    text-shadow: 2px 0 #000000, -2px 0 #000000, 0 2px #000000, 0 -2px #000000,
    1px 1px #000000, -1px -1px #000000, 1px -1px #000000, -1px 1px #000000;
  }

  
  main h2 {
    font-family: rock;
    color: rgb(26, 177, 54);
    text-align: center;
    font-size: 3rem;
  }
  
  main h3 {
    text-align: center;
  }
  
  main .greeting {
    position: absolute;
    left: 0;
    height: 100vh;
    width: 100%;
    background-image: url('../images/matrix-g7eee74277_1920.jpg');
    background-repeat: repeat-y;
    background-size: cover;
  }
  
  main .greeting--image {
    object-fit: contain;
    object-position: right;
    width: 50%;
  }
  
  main .greeting--text {
    color: white;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 50px;
  }
  
  .about-me p {
    text-align: left;
  }
  
  .about-section {
    font-size: 1.6rem;
    text-align: left;
    margin-top: 100vh;
  }
  
  .grid-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .work-item {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .project {
    display: flex;
    align-items: center;
    margin: auto;
    margin: 50px;
    flex: 1;
  }

  .project img {
    width: 50%;
  }

  .project-description {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex: 1;
  }

  .project-description p { 
    width: 60%;
    font-size: 20px;
  }

  .project-description h2 {
    margin-top: 0px;
    font-size: 33px;
  }

  .project-button {
    width: 70%;
    display: flex;
    justify-content: space-evenly;
    gap: 10px;
  }

  .project-button button {
    display: flex;
    align-items: center;
    font-size: 17px;
    padding: 5px 20px;
    font-weight: 700;
    border: none;
    background-color: green;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
  }

  .project-button button:hover {
    opacity: 0.7;
  }

  .project-button img {
    margin: 0 10px;
    width: 30px;
    height: 30px;
  }

  
  .middle {
    transition: .0.7s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    width: 400px;
  }
  
  .grid-image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
  }

  .middle button {
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    border-radius: 5px;
    padding: 10px 30px;
    font-size: 15px;
    font-weight: 700;
    border: none;
    transition: transform 0.15s;
    white-space: nowrap;
    background-color: black;
    color: white;
  }
  
  .name-of-project {
    margin-bottom: 50px;
    font-size: 30px;
  }

  .project-name-mobile {
    display: none;
  }

  .button-mobile {
    display: none;
  }

  #contact-section {
    margin: 0px !important;
  }

  .warning {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  .warning-image {
    width: 30px;
    height: 30px;
  }

  .stop {
    display: block;
    font-size: 3rem;
    font-weight: 700;
    color: rgb(204, 0, 0);
  }

  .gifNeo {
    width: 50%;
  }

  .contact h2 {
    text-shadow: 2px 0 #000000, -2px 0 #000000, 0 2px #000000, 0 -2px #000000,
    1px 1px #000000, -1px -1px #000000, 1px -1px #000000, -1px 1px #000000;
  }

  .contact-part {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    height: 50vh;
    width: 70vw;
    position: relative;
  }

  .contact-links-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .contact-links-container li {
    font-size: 2rem;
    list-style: none;
    padding: 40px;
    display: flex;
    gap: 50px;
  }

  .contact-links-container a {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'rock', cursive;
    text-decoration: none;
    color: white;
    font-size: 3rem;
    font-weight: 700;
  }

  .contact-links-container img {
    width: 50px;
    height: 50px;
  }

  .contact-form-container h3 {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    color: white;
    text-shadow: none;
    margin: 0;
  }

  .form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 320px;
  }

  #name {
    margin: 0;
  }

  #user_name, #user_email, #message {
    padding: 20px 40px;
    width: 230px;
    background-color: #3d3d3d;
    border: none;
    margin-top: 20px;
    border-radius: 10px;
    color: white;
  }

  .send-button {
    font-family: rock;
    margin-top: 20px;
    font-size: 25px;
    padding: 10px 70px 10px 70px;
    font-weight: 700;
    border-radius: 10px;
    transition: transform 0.15s;
    white-space: nowrap;
    background-color: rgb(62, 62, 62);
    color: white;
    cursor: pointer;
    box-shadow: inset 3px 5px 0px rgb(179, 179, 179);
    transition: box-shadow 0.15s;
  }

  .send-button:active {
    box-shadow: inset 0px 0px 0px rgb(179, 179, 179);
  }

  #message-send {
    position: fixed;
    bottom: 20px;
    right: 50px;
    background-color: #3d3d3d;
    height: 50px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 10px;
    font-size: 20px;
  }
}